<template>
  <div id="app">
    <router-view v-if="!showErrorPage"></router-view>
    <error-page v-if="showErrorPage"></error-page>
  </div>
</template>

<script>
	import ErrorPage from './components/error-page.vue';
	export default {
		name: 'app',
		components: {
			ErrorPage
		},
		data() {
			return {
				showErrorPage: false,
				urlKeys: 'https://spunity.github.io/collection/json/keys.json',
				urlData: 'https://spunity.github.io/collection/json/data.json'
			}
		},
		methods: {
			getDataKeys() {
				{
					this.$http.get(this.urlKeys).then(response => {
						this.$root.keys = response.body;
					}, () => {
						this.showErrorPage = true;
					});
				}
			},
			getDataMovies() {
				{
					this.$http.get(this.urlData).then(response => {
						this.$root.data = response.body;
					}, () => {
						this.showErrorPage = true;
					});
				}
			}
		},
		created() {
			this.getDataKeys();
			this.getDataMovies();
		}
	}
</script>

<style>
	body {
		background-image: url('./image/background.png');
	}
</style>
