<template>
	<div class="search">
		<form class="form-search" @submit.prevent="searchMovie">
			<div class="form-group row">
				<label for="search_input" class="col-sm-2 col-form-label label">Найдено <strong>{{ amountMovies }}</strong> фильмов</label>
				<div class="col-sm-8">
					<input class="form-control" id="search_input" type="search" aria-describedby="emailHelp" placeholder="Введите название фильма или имя режиссера" v-model="userText">
				</div>
				<div class="col-sm-2">
					<button type="submit" class="btn btn-primary ">Найти</button>
				</div>
			</div>
		</form>
	</div>
</template>

<script>
	export default {

		name: 'search',

		data () {
			return {
				userText: '',
				data: this.$root.data,
				movies: []
			}
		},
		methods: {
			searchMovie() {
				if (this.userText === '') {
					this.movies = [];
					this.$root.buffer.$emit('search_result', this.movies);
					return;
				}
				this.movies = this.data.filter((item) => {
					let userText = this.userText.toLowerCase(),
					moviesText = item.name.toLowerCase(),
					directorText = item.director.toLowerCase()
					;
					return moviesText.indexOf(userText) > -1 || directorText.indexOf(userText) > -1;
				})
				this.$root.buffer.$emit('search_result', this.movies);
			},
			setData(filteredData) {
				this.data = filteredData;
			}
		},
		computed: {
			amountMovies() {
				return this.movies.length;
			}
		},
		created() {
			this.$root.buffer.$on('filter_search', this.setData);
		}
	}
</script>

<style lang="css" scoped>
.label {
	background-color: #fff;
	border-radius: .5rem;
}
.form-search {
	width: 80%;
	margin: auto;
	margin-top: 2rem;
}
.test-result {
	font-size: 1.5rem;
	font-weight: bold;
	text-align: center;
}
</style>

