<template>
	<div class="movie-page">
		<img class="banner" :src="dataMovie.linkImage" alt="Banner">
		<div class="container-content">
			<div class="string-content" :key="i" v-for="(text, i) of arrTitlesForPage">
				<p class="content title"> {{ text }} </p>
				<p class="content value"> {{ arrDataForPage[i] }} </p>
			</div>
		</div>
	</div>
</template>

<script>
	export default {

		name: 'movie-page',

		data () {
			return {
				data: this.$root.data,
				keys: this.$root.keys,
				dataMovie: {},
				arrDataForPage: [],
				arrTitlesForPage: [
					'Название',
					'Год',
					'Жанр',
					'Режиссер',
					'Страна',
					'Продолжительность'
				]
			}
		},
		methods: {
			transformData() {
				let self = this,
				obj = this.dataMovie
				;
				for (let prop in obj) {
					if (prop === 'name' || prop === 'year' || prop === 'director' || prop === 'duration') {
						self.arrDataForPage.push(obj[prop]);
					} else if (prop === 'genreId' || prop === 'countryId') {
						let text = self.readId(prop)
						;
						self.arrDataForPage.push(text);
					}
				}
			},
			readId(propOfObj) {
				let arr = this.dataMovie[propOfObj],
				objKeys = this.keys,
				isGenre = propOfObj.indexOf('genre') > -1,
				str = ''
				;
				if (isGenre) {
					arr.forEach(item => {
						str+= objKeys['genre'][item] + ', ';
					})
					return str.slice(0, str.length - 2);
				} else {
					arr.forEach(item => {
						str+= objKeys['country'][item] + ', ';
					})
					return str.slice(0, str.length - 2);
				}
			},
			setDataMovie(index) {
				this.dataMovie = this.data[index];
			},
			getData(data, keys) {
				this.data = data;
				this.keys = keys;
			}
		},
		created() {
			let movieId = +this.$route.params.id,
			index = movieId - 1
			;
			this.setDataMovie(index)
			this.transformData();
		}
	}
</script>

<style lang="css" scoped>
.movie-page {
	width: 70%;
	border-radius: 1rem;
	background-image: url('../image/movie-page-back.png');
	margin: auto;
	margin-top: 3rem;
	padding: 2rem;
	display: flex;
	justify-content: space-between;
}
.banner {
	width: 12rem;
	height: 18rem;
	border-radius: .7rem;
}
.container-content {
	width: 70%;
}
.string-content {
	display: flex;
	justify-content: space-between;
}
.content {
	padding: .3rem;
	background-color: #fff;
	border-radius: .5rem;
	font-weight: bold;
	font-size: 1rem;
	color: #F46D1F;
}
.title {
	width: 40%;
}
.value {
	width: 50%;
}
</style>