<template>
	<div class="result">
		<ul class="list-unstyled list">
			<li class="alert alert-dark list-item" v-for="(movie, i) of arrMovies" :key="i">
				<span class="name">
					{{ movie.name }}
				</span>
				<button type="button" class="btn btn-info"><router-link class="link" :to="{name: 'movie', params: { id: movie.id } }">Подробнее</router-link></button>

			</li>
		</ul>
	</div>
</template>

<script>
	export default {

		name: 'result',

		data () {
			return {
				arrMovies: []
			}
		},
		created() {
			this.$root.buffer.$on('filter_result', this.appendDataFromFilter);
			this.$root.buffer.$on('search_result', this.appendDataFromSearch);
		},
		methods: {
			appendDataFromFilter(data) {
				this.arrMovies = data;
			},
			appendDataFromSearch(data) {
				this.arrMovies = data;
			}
		}
	}
</script>

<style lang="css" scoped>
.result {
	width: 70%;
	margin: auto;
	margin-top: 2rem;
}
.list-item {
	display: flex;
	justify-content: space-between;
}
.name {
	font-size: 1.5rem;
	font-weight: bold;
}
.link {
	text-decoration: none;
	color: #fff;
	font-weight: bold;
}
</style>