<template>
	
		<div class="filter-movies" @click="sendDataToSearch">
		
			<div class="header-filter">
				<button class="btn btn-danger" type="button" @click="fillButtons('genre', 'country')">Show filters</button>
				<transition name="show-matches">
					<button class="btn show-all btn-danger" type="button" @click="showResults" v-show="isMatches">Show {{ amountMatches }} {{ textForAmountMatches }} </button>
				</transition>
			</div>
			<transition name="filter">
				<div class="block-filters alert alert-secondary" v-show="showFilters.status" @click="filterData">
					<div class="one-filter" v-for="(filter, k) of arrFilters" :key="k">
						<div class="block-all-on">
							<button type="button" class="btn" :class="{'btn-secondary': filter.status, 'btn-danger': !filter.status}" :key="k" @click="allButtonsOn(filter)">{{ filter.name }}: {{ filter.addText }} </button>
						</div>
						<div class="btn-group block-separate-buttons" @click="checkAllButtonsOn(filter)">
							<button type="button" class="btn" :class="{'btn-secondary': !button.status, 'btn-danger': button.status}" @click="button.status = !button.status" v-for="(button, i) of filter.buttons" :key="i"> {{ button.text }} </button>
						</div>
					</div>
				</div>
			</transition>
		</div>
	
</template>

<script>
	export default {

		name: 'filter-movies',

		data () {
			return {
				data: this.$root.data,
				keys: this.$root.keys,
				arrFilters: {
					year: {
						name: 'Год',
						addText: 'all on',
						status: false,
						buttons: [
							{
								id: 1,
								text: 'До 2000 года',
								status: false
							},
							{
								id: 2,
								text: 'Начиная с 2000 года',
								status: false
							}
						]
					},
					genre: {
						name: 'Жанр',
						addText: 'all on',
						status: false,
						buttons: []
					},
					country: {
						name: 'Страна',
						addText: 'all on',
						status: false,
						buttons: []
					}
				},
				showFilters: {
					status: false,
					count: 0
				},
				filteredData: [],
				urlKeys: 'https://spunity.github.io/collection/json/keys.json',
				urlData: 'https://spunity.github.io/collection/json/data.json',
				amountMatches: 0
			}
		},
		computed: {
			isMatches() {
				if (this.showFilters.status) {
					return this.filteredData.length > 0;
				}else {
					return false;
				}
			},
			textForAmountMatches() {
				return this.amountMatches > 1 ? 'Matches' : 'Match';
			}
		},
		methods: {
			fillButtons(...args) {
				this.showFilters.status = !this.showFilters.status;

				if (this.showFilters.count < 1) {
					this.showFilters.count++;
					for (var i = 0; i < args.length; i++) {
						let ButtonsArr = this.arrFilters[args[i]]['buttons'];

						for (let key in this.keys[args[i]]) {
							let obj = {};

							obj.id = key;
							obj.text = this.keys[args[i]][key];
							obj.status = false;

							ButtonsArr.push(obj);
						}
					}
				}
			},
			allButtonsOn(obj) {
				obj.status = !obj.status;
				obj.addText = (!obj.status) ? 'all on' : 'all off';
				for (let button of obj.buttons) {
					button.status = obj.status;
				}
			},
			checkAllButtonsOn(obj) {
				let arrUnselectedButtons = obj.buttons.filter(button => {
					return !button.status;
				});

				if (!arrUnselectedButtons.length || arrUnselectedButtons.length === obj.buttons.length) {
					obj.status = !obj.status;
					obj.addText = (!obj.status) ? 'all on' : 'all off';
				}
			},
			filterData() {
				this.filteredData = this.data.filter((item) => {
					let yearCondition = false,
					genreCondition = false,
					countryCondition = false,
					yearButtons = this.arrFilters.year.buttons,
					genreButtons = this.arrFilters.genre.buttons,
					countryButtons = this.arrFilters.country.buttons
					;
					
					yearCondition = item.year < 2000 && yearButtons[0].status || item.year >= 2000 && yearButtons[1].status;
					item.genreId.forEach(function(button) {
						if (genreButtons[button - 1].status) {
							genreCondition = true;
						}
					});
					item.countryId.forEach(function(button) {
						if (countryButtons[button - 1].status) {
							countryCondition = true;
						}
					})

					return yearCondition && genreCondition && countryCondition;
				});

				this.amountMatches = this.filteredData.length;
				if (!this.filteredData.length) {this.showResults()}
			},
			showResults() {
				this.$root.buffer.$emit('filter_result', this.filteredData);
			},
			sendDataToSearch() {
				let sendingData = this.filteredData
				;
				if (!this.showFilters.status) {sendingData = this.data}
				this.$root.buffer.$emit('filter_search', sendingData);
			}/*,
			getDataKeys() {
				{
					this.$http.get(this.urlKeys).then(response => {
						console.log(response.body);
					}, response => {
						alert(response.body);
					});
				}
			},
			getDataMovies() {
				{
					this.$http.get(this.urlData).then(response => {
						alert(response.body);
					}, response => {
						alert(response.body);
					});
				}
			}*/
		},
		created() {
			//this.getDataKeys();
			//this.getDataMovies();
		}
	}
</script>

<style lang="css" scoped>
.filter-movies {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.header-filter {
	width: 20%;
	display: flex;
	justify-content: space-between;
}
.hidden {
	visibility: hidden;
}
.show-all {
	margin-left: 1rem;
}
.block-filters {
	width: 80%;
	margin: 1rem auto;
	text-align: left;
}
.one-filter {
	padding: .5rem;
}
.block-all-on {
	display: inline-block;
	padding-right: 1.5rem;
	border-right: 1px solid #444;
}
.block-separate-buttons {
	padding-left: 1.5rem;
}

/*animation*/
.filter-enter {
	transform: translateX(-140%);
}
.filter-enter-active, .filter-leave-active {
	transition: all .5s;
}
.filter-leave-to {
	transform: translateX(140%);
}

.show-matches-enter, .show-matches-leave-to {
	transform: scale(0);
}
.show-matches-enter-active, .show-matches-leave-active {
	transition: transform .5s;
}
</style>